.works {
    flex-grow: 1;
  }
  
  .works h1 {
    margin-bottom: 20px;
  }

  .download {
    color: #444444;
    background: #F3F3F3;
    border: 1px #DADADA solid;
    padding: 5px 10px;
    border-radius: 2px;
    font-weight: bold;
    font-size: 15pt;
    outline: none;
    text-align: center;
    margin-bottom: 50px;
}

.download:hover {
    border: 1px #C6C6C6 solid;
    box-shadow: 1px 1px 1px #EAEAEA;
    color: #333333;
    background: #F7F7F7;
    
}

.download:active {
    box-shadow: inset 1px 1px 1px #DFDFDF;   
}



