
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Sacramento&display=swap');
@import url(http://fonts.googleapis.com/css?family=Bitter:400,700);
.Typewriter{
  margin: 50px auto;
	text-align: center;
	text-shadow: -1px -1px 0px rgba(7, 7, 7, 0.048), 1px 1px 0px rgba(0,0,0,0.8);
	color: rgb(15, 15, 15);
	opacity: 0.4;
	font: 700 60px 'Bitter';
}

.home {
    flex-grow: 1;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .sub{
    color: black !important;
    text-transform: lowercase;
  }

  h1 typewriter {
    font-size: 90px;
  }

  
  .home .title {
    width: 100%;
    font-size: 18px;
    text-align: center;
    font-family: "Righteous", cursive;
  }
  
  .home .title p {
    line-height: 50px;
    font-family: "Righteous", cursive;
    color: white;
  }
  
  .home .title button {
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    margin-top: 30px;
    color: #ffffff;
    padding: 10px 15px;
    border-radius: 999px;
    background-color: black;
    font-family: "Righteous", cursive;

  }
  
  .home .person {
    width: 100%;
    margin-top: 20px;
  }
  
  .home .person img {
    width: 70%;
    border:2px solid #fff;
      box-shadow: 10px 10px 5px black;
      -moz-box-shadow: 10px 10px 5px black;
      -webkit-box-shadow: 10px 10px 5px black;
      -khtml-box-shadow: 10px 10px 5px black;
      display: flex;
      margin: 0 auto;
  }

  .button:hover{
      transform: scale(1.1);
  }
  
  @media (min-width: 800px) {
    .home {
      flex-direction: row;
      justify-content: space-between;
    }
    .home .title,
    .home .person {
      width: 50%;
      margin-top: 0;
      text-align: left;
      font-size: 25px;
    }
    .home .person img {
      width: 100%;
    }
  }

  .main{
    font-size: 3rem;
  }

  .sub{
    font-size: 2.5rem;
    text-align: center;
    position: relative;
    
   
    
  }